.card {
  background-color: green;
  height: min-content;
  width: min-content;

  border-radius: 15px;

  box-shadow: 2px 5px 20px 2px #888888;
}

.card-title {
  font-size: 20px;
  margin-left: 5px;
  margin-right: 5px;
  overflow: hidden;
  white-space: nowrap;
  color: white;
}

.card-content-images {
  white-space: nowrap;
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: space-evenly;
}

.dice-img {
  align-items: center;
  margin: 5px;
  width: 48px;
  height: 48px;
  padding: 0;
  border-radius: 5px;
}

.dice-img > img {

  width: 48px;
  height: 48px;
}

.dice-img img {
  width: 48px;
  height: 48px;
  box-shadow: 2px 5px 20px 2px #000000;

}

.diceroll-button-container {
  float: center;
  width: fit-content;
  margin-right: auto;
  margin-left: auto;
  margin-top: 25px;
  margin-bottom: 25px;
}

.main-menu-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  float: center;
  width: fit-content;
  margin-right: auto;
  margin-left: auto;
  margin-top: 50px;
  margin-bottom: auto;
}

.yahtzee-container {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
}

.score-table {
  table-layout: fixed; 
  width: 100%;
  
  border-collapse: separate;
  border-spacing: 0;
}

.table-data {
  height: 50px;
  border: 1px solid gray;
  text-align: center;

}

.score-card {
  background-color: rgb(223, 223, 223);
  min-width: 300px;
  max-width: 600px;

  border-radius: 15px;

  box-shadow: 2px 5px 20px 2px #888888;
}

@media only screen and (min-width: 1300px) {
  .score-card-container {
    padding-right: 50px;
    float: right;
  
  }
  .PlayerCards {
    float: left;
    padding-left: 50px;
  }
}



@media only screen and (max-width: 1300px) {
  .score-card-container {

    float: center;
    width: fit-content;
    margin-right: auto;
    margin-left: auto;
  }
  .PlayerCards {
    float: center;
    width: fit-content;
    margin-right: auto;
    margin-left: auto;
  }
  .yahtzee-container {
    flex-direction: column;
  }
  .table-data { 
    min-height: 50px;
    max-height: 100px;
  }
}

.w-340 {
  width: 340px;
}

.h-100 {
  height: 100px;
}

.roll-dice {
  animation-name: roll-dice-animation;
  animation-duration: 10s;
}

.table-head {

    border-top: none;
  border: 1px solid gray;
}

.player-amount-input {

  box-shadow: 2px 5px 20px 2px #000000;
  float: center;
  width: fit-content;
  margin-right: auto;
  margin-left: auto;

  width: 50px;
  height: 50px;

  font-size: 1.5em;
  text-align: center;

  border-radius: 4px;
  border: none;
  background-color: lightgray;
}

.player-card-li {
  list-style: none;
  margin-top: 20px;
}



.player-btn-container {
  float: center;
  width: fit-content;
  margin-right: auto;
  margin-left: auto;
}

.player-btn {

  width: 50px;
  height: 50px;

  font-size: 1.5em;
}

.roll-dice-btn {
  width: 250px;
  height: 50px;
  background-color: rgb(60, 73, 255);
  color: white;
  font-size: 1.5em;

  box-shadow: 2px 5px 20px 2px #000000;
  border-radius: 15px;
  border: none;

}

.table-head:first-child {
  border-radius: 15px 0 0 0;

  border-collapse: separate;
}

.table-head:last-child {
  border-radius: 0 15px 0 0;

  border-collapse: separate;
}

.table-head:only-child{
  border-radius: 15px 15px 0 0;

  border-collapse: separate;
}

@keyframes blink-cell {
  0% {
    background-color: rgba(0, 0, 0, 0);
  }
  100% {

    background-color: rgba(0, 0, 0, 0.15);
  }
}

@keyframes float-dice {
  0% {
    box-shadow: 0px 0px 0px 0px #000000;
  }
  100% {
    box-shadow: 2px 5px 20px 2px #000000;
  }
}

@keyframes roll-dice-animation {

  10% {

    box-shadow: 2px 5px 20px 2px #000000;
  }

  20% {
    transform: rotate(50deg);

    box-shadow: 2px 5px 20px 2px #000000;
  }

  80% {
    transform: rotate(3600deg);

    box-shadow: 2px 5px 20px 2px #000000;
  }

  90% {
    transform: rotate(3800deg);

    box-shadow: 2px 5px 20px 2px #000000;
  }

  100% {
    transform: rotate(3850deg);
  }
}